.admin__table{
    width: 100%;
    border-spacing: 0;

    svg{
        cursor: pointer;
    }
    
    &, &__th, &__td{
        border: 1px solid rgba(0,0,0,0.1);
    }

    &__th, &__td{
        padding: 5px 10px;
        text-align: left;
    }

    &__th{
        color:rgb(0, 173, 253);
    }

    &__td, &__th{
        &:first-child{
            width: 15px;
        }
        &:nth-child(2){
            width: 60px;
            a{
                margin-right: 5px;
            }
        }
    }
}