.timetable_card {
    width: 215px;
    text-align: center;

    p {
        margin-bottom: 5px;
        line-height: 1.8em;
    }

    &__img {
        width: 100%;

        img {
            width: 48%;
        }
    }

    &__age,
    &__days {
        font-weight: bold;
    }

    &__btn {
        line-height: 1;
        background-color: red;
        font-size: 15px;
        padding: 12px 24px;
        -webkit-border-radius: 3px;
        border-radius: 3px;
        border: none;
        color: #fff;
        -webkit-transition: all .3s;
        -o-transition: all .3s;
        transition: all .3s;
        cursor: pointer;

        &:hover {
            background-color: darken(red, 15%);
        }
    }
}