.attendance {

    &__table {
        border-spacing: 0;

        svg {
            cursor: pointer;
        }

        &,
        th,
        td {
            border: 1px solid rgba(0, 0, 0, 0.1);
        }

        th,
        td {
            padding: 5px 10px;
            text-align: left;
        }

        th {
            color: rgb(0, 173, 253);
        }
    }
}