.header {
    height: 50px;
    margin-bottom: 30px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #2E384D;
    color: white;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 14px;

    &-link {
        color: white;
        cursor: pointer;
    }

    &__main_menu {
        width: 500px;
        display: flex;
        justify-content: space-between;

        &-item {
            height: 50px;
            display: flex;
            align-items: center;
        }

        .submenu_general {
            position: relative;

            &:hover .submenu {
                display: block;
            }

            .submenu {
                display: none;
                border: 1px solid black;
                position: absolute;
                top: 50px;
                background-color: rgba(black, 0.5);

                &__item {
                    color: white;
                    padding: 5px 12px;
                    text-transform: uppercase;
                    font-weight: 600;

                    &:hover {
                        background-color: rgba(black, 0.6);
                        
                    }
                }
            }
        }
    }
}