.news_card{
    width: 30%;

    &__title{
        font-size: 24px;
        margin-bottom: 10px;

        &:hover{
            color: #f6bb3a;
        }
    }

    &__date_author{
        display: flex;
        font-size: 0.8em;
        margin-bottom: 10px;

        &__date{
            margin-right: 10px;
        }
    }

    &__desc{
        margin-bottom: 5px;
    }

    &__link{
        color: #5373d4;
        font-weight: bold;
    }
}