.timetable {
    margin-bottom: 50px;

    &__cards_wrapper {
        margin: 0 auto;
        width: 90%;
        height: calc(100vh - 80px);
        display: flex;
        align-items: center;
    }

    &__form {
        &_wrapper {
            margin: 0 auto;
            width: 60%;
        }

        h2{
            margin-bottom: 20px;
        }

        label {
            font-weight: bold;
            padding-bottom: 3px;
            line-height: 28px;
        }

        &__input {
            width: 100%;
            line-height: 18px;
            padding: 6px 10px;
        }
    }
}