@import 'variables';
@import 'normalize';

@import 'components/all';
@import 'pages/all';

#root{
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.mainContainer{
    width: 80%;
    margin: 0 auto;
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
}

.form__field{
    margin-bottom: 20px;
}

.form__btn {
    background-color: black;
    color: white;
    border: none;
    padding: 6px 11px;
    border-radius: 4px;
    cursor: pointer;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;

    &:hover {
        background-color: lighten(black, 20%);
    }
}

.testMap{
    position: absolute;
    left: 0;
    width: calc(100vw - 20px);
}